html {
  background-image: linear-gradient(to right, #d53369, #cbad6d);
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Lato', sans-serif;
}

h2 {
  margin-bottom: 0.2em;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 3em;
  font-size: 24px;
  background-color: #EEEEEE;
  border-radius: 5%;
  -webkit-box-shadow: 5px 0px 13px 3px rgba(0,0,0,0.26); 
  box-shadow: 5px 0px 13px 3px rgba(0,0,0,0.26);
  width: 50vw;
}

.resultborder {
  border: solid;
  padding: 0.75em;
  margin: 0.5em;
  background-color: white;
}

#timeResult {
  font-size: 32px;
}

#shiftResult {
  font-size: 32px;
  margin: 0.8em;
}

.forms {
  display: flex;
  flex-direction: row;
  margin-top: -1em;
}

.form {
  display: flex;
  flex-direction: column;
  margin: 1em;
}

input {
  width: 9em;
  height: 2.1em;
  margin-top: 0.5em;
}

button {
  background-color: #FFFFFF;
  border: 1px solid #222222;
  border-radius: 8px;
  box-sizing: border-box;
  color: #222222;
  cursor: pointer;
  display: inline-block;
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  margin: 0.3em;
  outline: none;
  padding: 10px 26px;
  position: relative;
  text-align: center;
  text-decoration: none;
  touch-action: manipulation;
  transition: box-shadow .2s,-ms-transform .1s,-webkit-transform .1s,transform .1s;
  user-select: none;
  -webkit-user-select: none;
  width: auto;
}

button:focus-visible {
  box-shadow: #222222 0 0 0 2px, rgba(255, 255, 255, 0.8) 0 0 0 4px;
  transition: box-shadow .2s;
}

button:active {
  background-color: #F7F7F7;
  border-color: #000000;
  transform: scale(.96);
}

button:disabled {
  border-color: #DDDDDD;
  color: #DDDDDD;
  cursor: not-allowed;
  opacity: 1;
}